"use client";

import { useEffect, useRef } from "react";
import { Renderer, Program, Mesh } from "ogl";

export default function Iridescence({ color, speed, amplitude, mouseReact, ...rest }) {
  const containerRef = useRef<HTMLDivElement>(null);
  const programRef = useRef<Program | null>(null); // Hoist the program
  const rendererRef = useRef<Renderer | null>(null);
  const animateIdRef = useRef<number>();

  useEffect(() => {
    if (!containerRef.current) return;

    const renderer = new Renderer();
    rendererRef.current = renderer;
    const gl = renderer.gl;

    // Initialize your shaders here (vertexShader, fragmentShader)
    const vertexShader = `...`;
    const fragmentShader = `...`;

    const program = new Program(gl, {
      vertex: vertexShader,
      fragment: fragmentShader,
      uniforms: {
        uTime: { value: 0 },
        uColor: { value: new (window as any).Color(...(color || [1, 1, 1])) },
        uResolution: { value: new (window as any).Color(gl.canvas.width, gl.canvas.height, gl.canvas.width / gl.canvas.height) },
        uAmplitude: { value: amplitude },
        uSpeed: { value: speed },
      },
    });
    programRef.current = program;

    // Create mesh, scene, etc.
    const mesh = new Mesh(gl, { /* geometry, program */ });
    containerRef.current.appendChild(gl.canvas);

    function resize() {
      const scale = 1;
      const width = containerRef.current!.offsetWidth * scale;
      const height = containerRef.current!.offsetHeight * scale;
      renderer.setSize(width, height);
      if (programRef.current) {
        programRef.current.uniforms.uResolution.value = new (window as any).Color(gl.canvas.width, gl.canvas.height, gl.canvas.width / gl.canvas.height);
      }
    }
    window.addEventListener("resize", resize);
    resize();

    function update(t: number) {
      animateIdRef.current = requestAnimationFrame(update);
      if (programRef.current) {
        programRef.current.uniforms.uTime.value = t * 0.001;
      }
      renderer.render({ scene: mesh });
    }
    animateIdRef.current = requestAnimationFrame(update);

    function handleMouseMove(e: MouseEvent) {
      const rect = containerRef.current!.getBoundingClientRect();
      const x = (e.clientX - rect.left) / rect.width;
      const y = 1.0 - (e.clientY - rect.top) / rect.height;
      if (programRef.current) {
        (programRef.current.uniforms.uMouse as any).value[0] = x;
        (programRef.current.uniforms.uMouse as any).value[1] = y;
      }
    }
    if (mouseReact) containerRef.current.addEventListener("mousemove", handleMouseMove);

    return () => {
      cancelAnimationFrame(animateIdRef.current!);
      window.removeEventListener("resize", resize);
      if (mouseReact && containerRef.current)
        containerRef.current.removeEventListener("mousemove", handleMouseMove);
      containerRef.current?.removeChild(gl.canvas);
      gl.getExtension("WEBGL_lose_context")?.loseContext();
    };
  }, [color, speed, amplitude, mouseReact]);

  return <div ref={containerRef} {...rest} className="w-full h-full" />;
}